<script setup lang="ts">
import {AppGrid} from '@/components/grid';
import {MessageService} from '@/services/message.service';
import {pickGridColumns} from '@/logic/common';
import {messageGridConfig} from '@/logic/message-grid-config';
import { StreamingService } from '@/services/streaming.service';

const messageService = useService(MessageService);
const streamingService = useService(StreamingService);

const config = pickGridColumns(
  messageGridConfig,
  ['orderId', 'displayName', 'displayText', 'createdAt'],
  {
    title: 'Мои сообщения',
    getData: async () => {
      const data = await messageService.getMyMessages();
      await streamingService.resetUnreadCount();

      return data;
    },
  },
);
</script>

<template>
  <AppGrid :config="config" />
</template>
